import React, { useState } from "react";
import { NavLink, Outlet, useNavigate } from "react-router-dom";
import { Container } from "react-bootstrap";
import { Icon } from "@iconify/react";
import "./Layout.css";

export const Layout = () => {
  const [toggle, setToggle] = useState(true);
  const navigate = useNavigate();

  const toggleMenu = () => {
    setToggle((prevState) => !prevState);
  };

  const handleLogout = () => {
    localStorage.removeItem("authToken");
    navigate("/"); // Redirect to login page
  };

  return (
    <Container fluid className="bg-white p-0">
      <div className={`sidebar ${toggle ? "open" : "closed"}`}>
        <ul className="nav-links left-sidebar ps-0">
          <div className="logo-details">
            <p className={`info ${toggle ? "open" : "closed"}`}>Management</p>
            <span>
              <Icon
                icon="fa6-solid:bars"
                onClick={toggleMenu}
                style={{ cursor: "pointer" }}
              />
            </span>
          </div>
          <li className="mt-3">
            <NavLink to={`/layout/patients`}>
              <i className="bx bx-grid-alt">
                <Icon icon="lets-icons:user-alt-fill" width="24" height="24" />
              </i>
              <span className={`info ${toggle ? "open" : "closed"}`}>
                Patients
              </span>
            </NavLink>
          </li>
          <li>
            <NavLink to={`/layout/appointments`}>
              <i className="bx bx-grid-alt">
                <Icon icon="teenyicons:appointments-outline" />
              </i>
              <span className={`info ${toggle ? "open" : "closed"}`}>
                Appointments
              </span>
            </NavLink>
          </li>
          <li>
            <NavLink to={`/layout/communication`}>
              <i className="bx bx-grid-alt">
                <Icon icon="fluent:chat-multiple-16-filled" />
              </i>
              <span className={`info ${toggle ? "open" : "closed"}`}>
                Communication
              </span>
            </NavLink>
          </li>
          <li>
            <NavLink to={`/layout/survey`}>
              <i className="bx bx-grid-alt">
                <Icon icon="mdi-light:chart-bar" />
              </i>
              <span className={`info ${toggle ? "open" : "closed"}`}>
                Survey
              </span>
            </NavLink>
          </li>
          <li>
            <NavLink to={`/layout/payment`}>
              <i className="bx bx-grid-alt">
                <Icon icon="fluent:payment-48-regular" />
              </i>
              <span className={`info ${toggle ? "open" : "closed"}`}>
                Payment
              </span>
            </NavLink>
          </li>
          <li>
            <NavLink to={`/layout/manage`}>
              <i className="bx bx-grid-alt">
                <Icon icon="tdesign:user-add" />
              </i>
              <span className={`info ${toggle ? "open" : "closed"}`}>
                ManageUser
              </span>
            </NavLink>
          </li>
        </ul>
        <div className="mt-3">
          <div className="logout" onClick={handleLogout}>
            <i className="bx bx-grid-alt px-2">
              <Icon icon="feather:log-out" />
            </i>
            <span className={`info ${toggle ? "open" : "closed"}`}>Logout</span>
          </div>
        </div>
      </div>
      <main className="main-content">
        <Outlet />
      </main>
    </Container>
  );
};
